import React, {useEffect} from 'react';
import './history-board.css';
const historyBoard = props => {

    let arr = [];
    for (let i = 0; i <= props.step; i++) {
        arr = [...arr, i];
    }
    console.log(arr);
    return arr.map(step =>
        <li>
            <button className="button" key={step} onClick={() => props.backToStep(step)}>Jump to {step}</button>
        </li>
    )
}

export default historyBoard;